:root{
  --clr-primary: #234375;
  --clr-primary-transparent: #234375cc;
  --clr-secondary: #315ea5;
}
::-webkit-scrollbar-thumb {
  background: var(--clr-primary-transparent);
  border-radius: .3rem;
}
.container-element.playerlist::-webkit-scrollbar {
  width: .75rem;
  background: var(--clr-secondary);
  border-radius: .3rem;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("./bg.jpg"), linear-gradient(270deg, var(--clr-primary), var(--clr-secondary));
  background-size: cover;
}

#root{
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.container{
  position: relative;
  width: 95%;
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,.6);
  color: white;
  margin: auto;
  border-radius: 1rem;
  text-align: center;
}
.container * + * {
  margin-top: 1rem;
}
.titled::before{
  content: attr(data-title);
    position: absolute;
    top: 0;
    left: 1rem;
    transform: translateY(-50%);
    background: var(--clr-primary);
    color: white;
    padding: .2rem 1.5rem;
    border-radius: .3rem;
    font-weight: bold;
}
.share-link{
  position: relative;
  display: block;
  background-color: grey;
  cursor: pointer;
  border-radius: .3rem;
  padding: .2rem 1rem;
  font-style: normal;
}
.right{
  position: absolute;
  right: .5rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1rem;
  margin: 0;
}
.copied{
  color: green;
  transition: color .1s cubic-bezier(0.29, 0.64, 0.39, 0.89);
}
.room-container{
  display: grid;
  grid-template-columns: 20% 1fr;
  grid-template-rows: 30% 1fr;
  grid-template-areas: 
  "playerlist teams"
  "commands   teams";
  padding: 2rem;
  gap: 1rem;
}
.container-element{
  background-color: var(--clr-primary);
  width: 100%;
  height: 100%;
  margin: 0;
  margin-top: 0;
  border-radius: .3rem;
}
.playerlist{
  overflow-y: auto;
  grid-area: playerlist;
}
.playerlist ul{
  list-style-type: none;
  padding: 1rem;
}
.playerlist ul li{
  display: block;
  margin: .3rem;
  position: relative;
}
.playerlist ul li:before {
  content: '';
  background-color: red;
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border-radius: 50%;
  box-shadow: inset -10px 10px 10px -14px white;
}

.playerlist ul li.online:before{
  background-color: green;
}

.playerlist hr{
  margin-top: .5rem;
  border: 1px solid var(--clr-secondary);
}
.teams{
  grid-area: teams;
}
.commands{
  grid-area: commands;
}
@media only screen and (max-width: 600px) {
  .link {
    visibility: hidden;
    font-size: 0;
  }
  .link::after{
    visibility: visible;
    content: "Copy link";
    font-size: 1rem;
  }
}

@media only screen and (max-width: 1200px) {
  .room-container{
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "playerlist"
    "commands"
    "teams";
    overflow-y: auto;
  }
  .playerlist{
    height: 15rem;
    margin-top: 1rem;
  }
  .commands{
    height: unset;
    min-height: 20rem;
  }
  .teams{
    height: unset;
    margin-bottom: 1rem;
    min-height: 30rem;
  }
  .titled::before{
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
